<template>
  <div>
    <header v-if="isShow == 'MAIN'">
      <section class="news-top-bar">
        <div class="news-top-bar__title">Báo cáo</div>
      </section>
    </header>
    <div class="row bc-foreder" v-if="isShow == 'MAIN'">
      <div
        v-for="(item, index) in danhMucBaoCao1"
        :key="index"
        class="bc-foreder__sub"
        v-on:click="fn_GoDetail(item.id, item.name, item.type)"
      >
        <div class="bc-foreder__folder" v-if="item.type == 'FOLDER'"></div>
        <div class="bc-foreder__report" v-if="item.type == 'REPORT'"></div>
        <h4 :title="item.name">{{ item.name }}</h4>
        <p>{{ item.content }}</p>
      </div>
    </div>

    <div v-if="isShow == 'FOLDER'">
      <bao-cao-chi-tiet :objReport="objReport" @goBackReport="goBackReport" />
    </div>

    <div v-if="isShow == 'REPORT'">
      <bao-cao-chi-tiet-cap-2
        :objReportDetail="objReportDetail"
        @goBackReportList="goBackReport"
      />
    </div>
  </div>
</template>

<script>
import BaoCaoChiTiet from "./BaoCaoChiTiet";
import BaoCaoChiTietCap2 from "./BaoCaoChiTietCap2";
export default {
  name: "BaoCao",
  components: {
    "bao-cao-chi-tiet": BaoCaoChiTiet,
    "bao-cao-chi-tiet-cap-2": BaoCaoChiTietCap2,
  },
  data() {
    return {
      danhMucBaoCao1: [],
      isShow: "MAIN",
      objReportDetail: {},
    };
  },

  created() {
    this.$store.dispatch("BaoCao/layDanhSachBaoCao", 0).then((res) => {
      this.danhMucBaoCao1 = res.data.data.list;
    });
  },

  methods: {
    fn_GoDetail: function (id, name, type) {
      this.isShow = type;

      if (this.isShow == "FOLDER") {
        this.objReport = {
          id: id,
          name: name,
        };
      } else {
        const url = this.danhMucBaoCao1.filter((item) => item.id === id);

        if (url.length > 0) {
          this.objReportDetail = {
            url: url[0].report_pbi_url,
            name: url[0].name,
          };
        }
      }
    },

    goBackReport: function () {
      this.isShow = "MAIN";
    },
  },

  computed: {
    // ...mapGetters(["BaoCao/danhSachBaoCao"]),
    // danhMucBaoCao() {
    //   return this.$store.getters["BaoCao/danhSachBaoCao"];
    // },
  },
};
</script>

